@use 'reset';

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: url(../img/Coding-Challenge-Image.png);
	background-size: cover;
	display: grid;
    grid-template-columns: repeat(15, 1fr);
    grid-template-rows: repeat(4, 1fr);
    height: 100vh;
}

/* 2. Content ================================= */
.bar_color{
	background: rgb(117, 34, 3);
}

.bar_color{
	height: 100%;
	z-index: -1;
    grid-area: 1 / 1 / 5 / 4;
}

main{
    display: flex;
    flex-direction: column;
	grid-area: 2/2/4/8;
	justify-content: end;

	h1{
		color: #fff;
		text-transform: capitalize;
		font-size: 13em;
	}
	.articleBtn{
		width: fit-content;
		background: none;
		border-radius: 100%;
		border: none;
		position: absolute;
		margin-bottom: -42px;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		z-index: 1;
	}
	.articleDiv{
		transform-origin: bottom left;
		height: 0%;

		.articleHead{
			opacity: 0%;
			
		}
		.bar_article{
			opacity: 0%;
			
		}
		.article_content{
			opacity: 0%;
			
		}
	}
	.ctaLink{
		background: linear-gradient(to right, #752203, #834f3c, #ac7763);
		text-transform: uppercase;
		text-decoration: none;
		color: #fff;
		padding: 10px 5%;
		margin-top: auto;
		width: fit-content;
		opacity: 0%;
	}
	#inactive{
		margin-left: 50px;
	}
	#active{
		display: none;
	}
	#closed{
		animation: articleClose 1.3s linear ;
		overflow: hidden;

		.articleHead{
			opacity: 0%;
			
		}
		.bar_article{
			opacity: 0%;
			
		}
		.article_content{
			opacity: 0%;
			
		}
	}
	#opened{
		display: flex;
		flex-direction: column;
		overflow: hidden;
		height: 100%;
		padding: 13px 5%;


		.articleHead{
			opacity: 0%;
			justify-content: space-between;
			display: flex;


			button{
				width: 50px;
				background: none;
				border: none;
				padding: 0%;
				cursor: pointer;
				
				div{
					height: 5px;
					background: #752203;
					transform-origin: center;
					border-radius: 5px;
				}
				.xBtn_L{
					transform: rotateZ(45deg);
				}
				.xBtn_R{
					transform: rotateZ(-45deg);
				}
			}
		}
		.bar_article{
			opacity: 0%;

		}
		.article_content{
			opacity: 0%;

		}
		
	}

	.articleDiv{
		background: #fff;
		
		h2{
			text-transform: capitalize;
			font-size: 4.2em;
			color: #752203;
		}
		.bar_article{
			height: 5px;
			margin: 10px 0%;
			background: linear-gradient(to right, #752203, #752203, rgba(255, 255, 255, 0.534), rgba(255, 255, 255, 0));
		}
	}
}

#low{
	grid-area: 2/2/4/8;
	justify-content: end;
	
	.mainHead{
		animation: CloseDelay 1.3s linear 0s;
		opacity: 100%;
		transition: opacity 1.3s linear 1.3s;
	}
	.articleDiv{
		// animation: articleClose 1.3s linear;
		
		.articleHead{
			animation: fadeOut 1.3s linear;
		}
		.bar_article{
			animation: fadeOut 1.3s linear;
		}
		.article_content{
			animation: fadeOut 1.3s linear;
		}
		.ctaLink{
			animation: fadeOut 1.3s linear;
		}
	}
}
#high{
	grid-area: 2/2/4/8;
	justify-content: center;
	.mainHead{
		opacity: 0%;
		transition: opacity 1.3s linear;
		position: absolute;
	}
	.articleDiv{
		animation: articleOpen 1.3s ease-in-out;
	}
}
nav{
	grid-area: 4 / 15 / 5 / 16;
}

@keyframes articleOpen{
	0%{
		transform: scaleY(0);
		border-radius: 500px;
	}
	20%{
		border-radius: 250px;
	}
	40%{
		border-radius: 300px;
	}
	60%{
		border-radius: 100px;
	}
	80%{
		border-radius: 150px;
	}
	100%{
		border-radius: 10px;
	}
}
@keyframes articleClose{
	0%{
		height: 100%;
		transition: height 4.2s linear;
		border-radius: 0px;
	}
	30%{
		height: 50%;
		width: 75%;
		transition: height 4.2s linear;
		border-radius: 250px;
	}
	60%{
		height: 25%;
		width: 25%;
		transition: width 4.2s linear;
		transition: height 4.2s linear;
		border-radius: 360px;
		margin-left: 35px;
	}
	100%{
		height: 0%;
		width: 0%;
		transition: width 4.2s linear;
		transition: height 4.2s linear;
		border-radius: 500px;
		margin-left: 70px;
		margin-top: 20px;
	}
}

@keyframes fadeOut{
	0%{
		opacity: 100%;
	}
	50%{
		opacity: 50%;
	}
	100%{
		opacity: 0%;
	}
}

@keyframes closeDelay {
	0%{
		position: absolute;
	}
	100%{
		position: unset;
	}
}
@keyframes btnDelay {
	0%{
		display: none;
	}
	100%{
		display: flex;
	}
}